import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'
import Index from '@/components/welcome/index.vue'

import BookList from '@/components/role/list'

import UserList from '@/components/user/list'
import UserChangePwd from '@/components/user/changepwd'
import UserProfile from '@/components/user/profile'

import MenuList from '@/components/menu/list'

import FileList from '../components/file/List.vue'

import LogList from '../components/log/List'


// 
const Login = resolve => require(['@/components/Login'], resolve)

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/index',
      name: '',
      component: Index
    },
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/dashboard',
      leaf: true, // 
      menuShow: true,
      iconCls: 'fa fa-home', // class
      children: [
        {path: '/dashboard', component: Dashboard, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'fa fa-user', // class
      children: [
        {path: '/admin/user', component: UserList, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'fa fa-server', // class
      children: [
        {path: '/admin/menu', component: MenuList, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true,
      iconCls: 'fa fa-group',
      children: [
        {path: '/admin/role', component: BookList, name: '', menuShow: true},
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true,
      iconCls: 'fa fa-group',
      children: [
        {path: '/cms/file', component: FileList, name: '', menuShow: true},
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true,
      iconCls: 'fa fa-group',
      children: [
        {path: '/base/log', component: LogList, name: '', menuShow: true},
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-setting1',
      children: [
        {path: '/user/profile', component: UserProfile, name: '', menuShow: true},
        {path: '/user/changepwd', component: UserChangePwd, name: '', menuShow: true}
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-setting1',
      children: [
        {path: '/service',component: UserProfile,  redirect: 'http://baidu.com',target:'_blank',name: '', menuShow: true},
      ]
    },
  ]
})

router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith('/login')) {
    window.localStorage.removeItem('access-token')
    //window.localStorage.removeItem('access-user')
    next()
  } else if (to.path.startsWith('/index')) {
    next()
  } else {
    //let user = JSON.parse(window.localStorage.getItem('access-token'))
    let user = window.localStorage.getItem('access-token');
    if (!user) {
      next({path: '/login'})
    } else {
      next()
    }
  }
})

export default router



